<template>
  <div class="page subpage" id="login">
    <div class="close" @click="backAction">
      <van-icon name="cross"/>
    </div>
    <img class="logo" src="/images/logo3.jpg">
    <div class="handle">
      <input type="text" class="border" placeholder="" ref="user">
      <input type="password" placeholder="" ref="psd">
      <div class="confirm" @click="handleLoginAction"></div>
      <p class="reg-btn" @click="showRegAction">
        <span></span>
        <van-icon name="arrow"/>
      </p>
    </div>
    <p class="info"></p>
  </div>
</template>

<script>
import { Toast } from "vant";
import { mapActions } from "vuex";
export default {
  props: ["path"],
  methods: {
    ...mapActions(["loginAction"]),
    handleLoginAction() {
      // 
      // 
      let params = {
        username: this.$refs.user.value,
        password: this.$refs.psd.value
      };
      if (params.username && params.password) {
        //
        this.loginAction(params).then(result => {
          if (result == 0) {
            //
            //
            console.log("");
            this.$router.push(this.path);
            this.backAction();
          } else {
            //
          }
        });
      } else {
        Toast("");
      }
    },
    backAction() {
      this.$center.$emit("toggle-login");
    },
    showRegAction() {
      this.$emit("change");
    }
  }
};
</script>

