<template>
<div class="scroller-wrap" ref="wrap">
    <div class="scroller">
        <slot/>
    </div>
</div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
    name: 'scroller',
    methods: {
        // 
        scrollTo(y, duration){
            this.scroll && this.scroll.scrollTo(0, y, duration);
        }
    },
    mounted(){
        // dom
        // 
        // 
        let scroll = new BScroll(this.$refs.wrap, {
            click: true,
            tap: true
        });
        this.scroll = scroll;

        // 
        scroll.on('beforeScrollStart', ()=>{
            // console.log('');
            // 
            scroll.refresh();
        })
    }
}
</script>

